@import url(https://fonts.googleapis.com/css2?family=Cinzel&display=swap);
header{position:absolute;top:-2.5rem;left:50%;text-align:center;transform:translate(-50%, -50%)}header img{max-width:12rem;height:auto}

footer{text-align:center;font-size:1.6rem;color:white;position:absolute;width:32rem;bottom:-2.5rem;left:50%;transform:translate(-50%, 50%)}@media (max-width: 768px){footer{font-size:1.4rem}}

nav{position:absolute;left:-2.5rem;top:50%;transform:translate(-2.5rem, -50%);z-index:1000}nav ul li{position:relative;height:95px;left:-10px}nav ul li:first-child{left:-20px;height:75px;transition:1s}nav ul li:first-child:hover{left:6px}nav ul li:nth-child(2){left:-25px;transition:1s}nav ul li:nth-child(2):hover{left:6px}nav ul li:last-child{left:-44px;transition:1s}nav ul li:last-child:hover{left:6px}@media (max-width: 768px){nav ul li:last-child{left:-40px}}nav ul li a{color:white;position:absolute;transform:rotate(90deg);transition:1s;padding:10px 20px}nav ul li a:hover{border:1px solid #000;color:black;transform:rotate(0);background:#fff}

.soc{position:absolute;right:-2.5rem;top:50%;transform:translate(50%, -50%);transition:1s}.soc li{width:32px;height:32px;background:white}.soc li+li{margin-top:1rem}.soc a{background:transparent}.soc svg{transition:all 1s}.soc svg:hover{border:1px solid white}

.border{color:#fff;position:relative;width:100%;height:100%;border:5rem solid rgba(0,0,0,0.95)}

.contact-side{padding:2rem}.contact-side input,.contact-side label,.contact-side textarea{border-radius:0;display:block;width:100%;margin:0 auto}.contact-side input{margin-bottom:1rem;padding:.5rem 0}.contact-side textarea{margin-bottom:1rem}.contact-side label{font-size:1.4rem}@media (max-width: 768px){.contact-side label{font-size:1.2rem}}.contact-side button{padding:.5rem 0;width:100%;background:white;color:black}

.scene{position:absolute;margin:auto;top:0;right:0;bottom:0;left:0;width:100%;max-height:350px;perspective:1400px}#box{width:80%;height:100%;position:relative;transform-style:preserve-3d;transform:translateZ(-50px);transition:transform 1s}#box .home-side,#box .about-side{padding:7rem}.box__face{background:rgba(0,0,0,0.9);position:absolute;text-align:center;display:flex;justify-content:center;align-items:center;letter-spacing:5px}.box__face h1,.box__face h2{font-size:2.6rem;margin-bottom:1rem}@media (max-width: 768px){.box__face{font-size:1.6rem}.box__face h1,.box__face h2{font-size:2rem}}@media (max-width: 512px){.box__face{font-size:1.4rem}.box__face h1,.box__face h2{font-size:1.6rem}}.logo-wrapper{display:flex;background:white;width:50%;justify-content:space-around;padding:10px;margin:3.5rem auto 0}.logo-wrapper img{max-width:40px;height:auto}#message-side{transform:rotate(180deg)}#box.rotate{transition:transform 1s;transform:translateZ(-100px) rotateX(180deg)}.box__face--front,.box__face--back{width:80%;height:100%;left:22%}.box__face--top,.box__face--bottom{width:80%;height:100%;top:0;left:22%}#box.show-front{transform:translateZ(-50px) rotateY(0deg)}#box.show-top{transform:translateZ(-100px) rotateX(-90deg)}#box.show-bottom{transform:translateZ(-100px) rotateX(90deg)}.box__face--back{transform:rotateY(180deg) translateZ(175px)}.box__face--front{transform:rotateY(0deg) translateZ(175px)}.box__face--top{transform:rotateX(90deg) translateZ(175px)}.box__face--bottom{transform:rotateX(-90deg) translateZ(175px)}

html,body,#root,#root>div{height:100%}body{color:#fff;text-shadow:0 0 10px rgba(255,255,255,0.8);font-family:'Cinzel', serif;font-size:2rem}@media (max-width: 768px){body{font-size:1.6rem}}#root{background:white}a{color:black;text-decoration:none;cursor:pointer}button{cursor:pointer;background:transparent}html{box-sizing:border-box;font-size:62.5%}*,*:before,*:after{box-sizing:inherit}body,h1,h2,h3,h4,h5,h6,p,ol,ul{margin:0;padding:0;font-weight:normal}ol,ul{list-style:none}img{max-width:100%;height:auto}

